/*slide checkbox*/
.slide-btn {
  height: 30px;
  line-height: 30px; }
  .slide-btn input {
    width: 50px;
    height: 30px;
    line-height: 30px;
    margin: 0; }
    .slide-btn input:before {
      content: "";
      position: absolute;
      padding-left: 20px;
      width: 50px;
      height: 30px;
      line-height: 30px;
      border-radius: 15px;
      text-align: center;
      background-color: #ccc;
      color: #fff;
      box-sizing: border-box;
      -webkit-transition: background-color .4s linear; }
    .slide-btn input:after {
      content: "";
      position: absolute;
      display: inline-block;
      width: 28px;
      height: 28px;
      line-height: 28px;
      border-radius: 28px;
      text-align: center;
      background-color: #fff;
      box-sizing: border-box;
      -webkit-transform: translateX(1px);
      -webkit-transition: -webkit-transform .4s linear;
      margin-top: 1px; }
    .slide-btn input:checked:before {
      content: "";
      background-color: #fe007f !important;
      -webkit-transition: background-color 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.1) 0s; }
    .slide-btn input:checked:after {
      -webkit-transform: translateX(21px);
      -webkit-transition: -webkit-transform 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.1) 0s; }

